.slider {
  width: 100%;
  height: 100%;
  padding: 30px 0;

  color: #773d1d;

  border-top: 1px solid #773d1d;

  @include onTable {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 50px;
    border: none;
  }

  &__two {
    flex-direction: row-reverse;
    gap: 30px;
  }

  &__border {
    display: inline-block;
    min-width: 3px;
    height: 250px;
    background: #773d1d;

    @include onTableDo {
      display: none;
    }

    &-two {
      opacity: 0;
    }
  }

  &__wrapper {
    position: relative;
    display: flex;
    align-items: center;

    padding: 25px;
    width: 100%;

    @include onTable {
      min-width: 48%;
      max-width: 500px;
    }
  }

  &__container {
    width: 100%;
    height: 300px;
    overflow: hidden;

    @include onMobile {
      height: 400px;
    }

    @include onTable {
      min-width: 48%;
    }
  }

  &__img {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    z-index: 1;

    transition: 0.3s ease;
  }

  &__button {
    background: transparent;
    outline: none;
    border: none;
    z-index: 2;
  }

  &__block {
    width: 100%;
    height: 100%;
    padding: 30px 0;

    &-border {
      height: 2px;
      width: 100%;
      background-color: #773d1d;
    }
  }

  &__article {
    padding: 20px 0px;

    @include onTable {
      max-width: 30%;
      text-align: left;
    }
  }

  &__title {
    font-size: 36px;
  }

  &__paragraph {
    color: #a9a9a9;
    font-size: 15px;
    line-height: 1.5em;
  }
}
