.booking {
  min-height: 100vh;
  min-width: 100%;

  &__wrapper {
    padding: 30px 0;
  }

  &__img {
    width: 100%;
    height: 50vh;
    object-fit: cover;
  }

  &__form {
    display: flex;
    flex-direction: column;
    gap: 30px;

    margin: 0 auto;
    padding: 40px 25px;
    border: 2px solid #773d1d;

    @include onTable {
      max-width: 500px;
    }
  }

  &__input {
    padding: 10px 20px;
    width: 100%;
    outline: none;

    font-size: 14px;
    font-weight: 600;
    border: 2px solid #a9a9a9;
    border-radius: 4px;

    color: #a9a9a9;
    transition: all 0.5s linear;

    &:focus {
      border-color: #773d1d;
      color: #773d1d;
    }

    &::placeholder {
      color: inherit;
    }
  }

  &__label {
    display: flex;
    flex-direction: column;
    text-align: start;

    width: 100%;
    gap: 8px;
    font-size: 18px;
  }

  &__button {
    width: 100%;
    background: #773d1d;
    padding: 15px 25px;
    color: #fff;
  }
}
