.header {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  height: 75px;

  padding: 15px 25px;

  z-index: 2;

  transition: background 0.5s ease-in;

  &.scrolled {
    background: rgba(0, 0, 0, 0.5);
  }

  &__button {
    border: 2px solid #fff;
    outline: none;
    background: transparent;
    padding: 15px 25px;

    border-radius: 16px;
  }

  &__link {
    text-decoration: none;
    color: #fff;
  }
}
