.footer {
  background: #773d1d;
  padding: 15px 25px;

  &__wrapper {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
  }

  &__logo {
    width: 150px;
    height: 150px;
  }

  &__border {
    width: 100%;
    height: 2px;
    background: #fff;
    border-radius: 2px;

    margin: 20px 0;
  }

  &__list {
    display: flex;
    flex-direction: column;
    gap: 15px;
  }

  &__item {
    &:first-child {
      text-align: center;
    }

    &-link {
      display: flex;
      align-items: center;
      justify-content: center;
      gap: 20px;

      text-decoration: none;
      color: #fff;
    }

    &-span {
      font-size: 18px;
      line-height: 1.5em;
    }
  }
}
